.hourly-weather {
  position: relative;
  top: 5vh;
  width: 100%;
}

.hourly-data {
  align-items: center;
  padding: 3px 3px;
  border-radius: 10px;
  background-color: rgba(255,255,255,0.2);
  box-shadow: 3px 6px rgba(0,0,0,0.2); 
  margin-bottom: 5px;
}

#hourly-temp, #hourly-description {
  margin: 0 15px;
}